* {
  margin: 0;
}

body {
  padding: 1em 0;
  background-color: black;
}



/* NavBar.js */

.navi {
  background-color: rgb(0, 0, 0);
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.title {
  font-family: Snell Roundhand, cursive;
  color: #ffffff;
	font-size: calc((2.5 - 1) * 2vw + 1rem);
}

.top a {
  text-decoration: none;
  color: #ffffff;
}

.socials img{
	justify-content: space-between;
  max-width: calc((3 - 1) * 1vw + .5rem);
	text-decoration: none;
	color: #000000;
}

.logo {
  max-width: 10em;
  font-size: calc((2 - 1) * 1vw + .5rem);
}

.socials {
  color: #ffffff;
  text-decoration: none;
}

.socials a {
  margin: 1em;
  padding: .5em;
  text-decoration: none;
  color: #ffffff;
}

.nav-bar{
	display: flex;
  background: #000000;
  border-top: 1px solid white;
  border-bottom: 1px solid white;
	justify-content: space-evenly;	
}

.nav-bar a{
	color: white;
	text-decoration: none;
  padding: .5em 0px;
}

.nav-bar a:hover{
  text-shadow:  1px 1px 3px red, 
                3px 3px 3px white; 
}

/* Home.js */

.home {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}

/* Carousel.js */

.Carousel {
  padding: 1em 1em;
  box-shadow: 0em 0em 0em 1px rgb(255, 255, 255);
  margin: 1em 0em;
  width: 100%;
  color: white;
  display: flex;
  overflow-x: scroll;
}

.Carousel img {
  width: 125px;
  height: 125px;
  margin: 0em 1em;
}

.card {
  min-width: 300px;
  padding: .5em 0em;
  margin: 0em 1em;
  border: 1px solid white;
  display: flex;
  align-items: center;
  box-shadow: 0px 0px 8px 0px white;
}

.cardBody p {
  font-size: 14px;
  margin: 0em 1em;
}

/* About.js */

.About {
  max-height: 100%;
  max-width: 45%;
  align-items: center;
  background-color: #000000;
}

.front {
  margin: 0em 2em;
  position: relative;
  box-shadow: 0em 0em 4px 2px rgb(255, 255, 255);
}

#headshot {
  max-width: 100%;
}

.frontCap {
  text-align: center;
  position: absolute;
  top: 0;
  background-color: rgb(0, 0, 0, 1);
  width: 55%;
  font-size: 17px;
  padding: 3em 0em;
  border-radius: 0em 0em 1em 0em;
  color: #ffffff;
  box-shadow: 0em 0em 2px 1px rgb(255, 255, 255);
}

.frontCap p {
  padding: 0em 1em;
}

/* MotD.js */

.motd {
  text-align: center;
  max-height: 100%;
  max-width: 45%;
  color: #ffffff;
  box-shadow: 0em 0em 4px 2px rgb(255, 255, 255);
}

.mealTitle {
  position: absolute;
  background-color: rgb(0, 0, 0, .5);
  z-index: 1;
  width: 45%;
  box-shadow: 0em 0em 4px 0px rgb(255, 255, 255);
}

.motd h1 {
  margin: auto;
  width: 75%;
  background-color: rgb(0, 0, 0, .7);
}

.meal {
  position: relative;
}

#mealPic {
  max-width: 100%;
}

.mealText{
  box-shadow: 0em 0em 4px 0px rgb(255, 255, 255);
  position: absolute;
  text-align: center;
  background-color: rgb(0, 0, 0, .7);
  bottom: 0;
  width: 100%;
}

.mealCap {
  padding: 0em 5em;
  margin: .25em 0em;
}

.mealLink {
  background-color: #000000;
  color: #ffffff;
  cursor: pointer;
  margin-bottom: .5em;
}

@media only screen and (max-width: 600px){

  .navi {
    border-bottom: 1px solid white;
  }

  .top {
    border-bottom: 1px solid white;
  }

  .socials {
    text-align: center;
    width: 100px;
  }

  .socials a{
    margin: 0;
    padding: 2px 8px;
  }

  .mainRunner {
    position: relative;
    margin-top: .5em;
  }

  .mainRunner img {
    border-radius: .25em;
    max-width: 22em !important;
  }

  #Bio p {
    margin: -.8em;
    padding: .5em .5em;
    background-color: rgb(0, 0, 0, .5);
  }

  #Bio {
    background-color: rgba(0, 0, 0, .7);
    position:absolute;
    top: 0;
    left: 0;
    border-radius: .5em 0em;
    text-align: center !important;
    width: 185px;
    margin-left: 1em;
    padding: 5.25em .8em;
    font-size: 12px !important;
    border-right: 3px solid rgb(0, 0, 0, .7);
    border-bottom: 3px solid rgba(0, 0, 0, .7);
  }

}